.modal {
  display: none;
  position: fixed;
  
  right: 3.99%;
  z-index: 1;
  width: 90%;
  height: 100%;
  overflow: hidden;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);

  justify-content: center;
  flex-direction: column;
  align-items: center;
}


  
.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

